import React, {useEffect, useRef, useState} from "react";

const TitleFlow = (props) => {
    const rearLayer = useRef(null);
    const middleLayer = useRef(null);
    const frontLayer = useRef(null);

    const [rearLayerText, setrearLayerText] = useState(props.title);
    const [middleLayerText, setmiddleLayerText] = useState(props.title);
    const [frontLayerText, setfrontLayerText] = useState(props.title);

    let rearText = "";
    let middleText = "";
    let frontText = "";

    useEffect( ()=>{
      rearLayer.current.style.left = '-10000px';
      middleLayer.current.style.left = '-10000px';
      frontLayer.current.style.left = '-10000px';
      console.log(props.title);
      for(let i=0; i<4; i++){
          //eslint-disable-next-line
        rearText += props.title + " "
      }
      setrearLayerText(rearText);
      for(let j=0; j<8; j++){
          //eslint-disable-next-line
        middleText += props.title + " "
      }
      setmiddleLayerText(middleText);
      for(let k=0; k<16; k++){
          //eslint-disable-next-line
        frontText += props.title + " "
      }
      setfrontLayerText(frontText);
   },[props.title]);

   let display = props.show? "block" : "none";
    return (
      <div className="titleFlow" style={{display:display}}>
        <div className="rearLayer" ref={rearLayer}>{rearLayerText}</div>
        <div className="middleLayer" ref={middleLayer}>{middleLayerText}</div>
        <div className="frontLayer" ref={frontLayer}>{frontLayerText}</div>
        <div className="editMediaImageSideHeader" style={{color:"rgba(0,0,0,0.3)",textShadow:"0 3px 10px rgba(0, 0, 0, 0.3)"}}><legend>{props.title}</legend></div>
      </div>
    )
};

export default TitleFlow;
